
import {useState, useEffect} from 'react'

import {getLog} from '../../../../domain/codingPractice'

import LineChart from './lineChart'


const defaultMargin = {
		top : 20 ,
		right : 20,
		left : 20,
		bottom : 20
}

const defaultAxis = {
	left : 50,
	bottom : 50,
	margin : defaultMargin
}


function getTitle(keyword){

	switch(keyword){
		case 'count' :
			return `    `;
		case 'repetition' :
			return `    `;
	}

	return `     (${keyword})`;
}


export default function({width=1000, keyword='count', height, className}){

	const [data, setData] =  useState([0,10])
	const [axis, setAxis] = useState([])

	useEffect(()=>{
		getLog(Date.now(),365)
			.then(t => {
				setData(t.map(d => d[keyword]))
				setAxis(t.map(d => new Date(d['date'])))
			})
	},[data, keyword])

	return (
			<LineChart data={data} 
						axisData={axis}
						type={'time'}
						title={getTitle(keyword)}
						width={1000} height={height}
						nticks={4}
						axis={{ left:50, bottom : 50, margin: defaultMargin, type: 'time', tickformat : 'month'}}
						className={className}
			/>
		)
}